<template>
  <div class="wrap-nav index-wrap">
    <Header :htitle = "msg" :icoS="leftIco" :icobS="rightIco" :icoSS="icoShow"></Header>
    <div class="top">
      <p class="h-2">数据类型</p>
      <div class="switchtag">
        <span class="on">在线</span>
        <span>填报</span>
        <b></b>
      </div>
    </div>
    <div class="con">
        <div class="mid-a">
          <p class="h-4"><img src="../assets/hico.png">能源数据看板</p>
          <ul class="">
            <li>
              <p class="t-name">全社会能源消费总量</p>
              <p class="t-num">{{ dataList.totalSocietyEnergy }}</p>
              <p class="t-unit">tec</p>
              <p class="t-note">同比:{{ dataList.totalSocietyEnergyP }}%</p>
            </li>
            <li>
              <p class="t-name">能源消费总量</p>
              <p class="t-num">{{ dataList.totalEnergy }}</p>
              <p class="t-unit">tec</p>
              <p class="t-note">同比:{{ dataList.totalEnergyP }}%</p>
            </li>
            <li>
              <p class="t-name">工业用电量</p>
              <p class="t-num">{{ dataList.industrialElectricity }}</p>
              <p class="t-unit">万kw/H</p>
              <p class="t-note">同比:{{ dataList.industrialElectricityP }}%</p>
            </li>
            <li>
              <p class="t-name">工业总产值</p>
              <p class="t-num">{{ dataList.grossIndustrialOutput }}</p>
              <p class="t-unit">亿元</p>
              <p class="t-note">同比:{{ dataList.grossIndustrialOutputP }}%</p>
            </li>
            <li>
              <p class="t-name">产值单耗</p>
              <p class="t-num">{{ dataList.unitConsumption }}</p>
              <p class="t-unit">kgce/万元</p>
              <p class="t-note">同比:{{ dataList.unitConsumptionP }}%</p>
            </li>
          </ul>
        </div>
        <div class="mid-b">
          <div class="b-left">
            <p class="h-4"><img src="../assets/hico.png">热力分布图</p>
            <div class="fbt"><img src="../assets/fenbutu.png"></div>
          </div>
          <div class="b-right">
            <p class="h-4"><img src="../assets/hico.png">区域指标排名</p>
            <div class="chart">
                <highcharts-container :defOptions="optionsb" style="width: 100%;height: 3.333333rem;"></highcharts-container>
            </div>
          </div>
        </div>
        <div class="bot">
          <p class="h-4"><img src="../assets/hico.png">指标趋势变化</p>
          <div class="chart">
            <highcharts-container :defOptions="options" style="width: 100%;height: 5.333333rem;"></highcharts-container>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
  import Header from '@/components/part/Header'
  import HighchartsContainer from '../components/part/chart'
  import data from '../../static/data/dataFirst'

  export default {
    name: 'index',
    data() {
      return {
        //顶部标题
        msg: '济宁市能耗首页',
        leftIco: 'backI', //closeI 关闭
        icoShow: false, //左边图标是否
        rightIco: false, //右边图标是否
        dataList: {}, //数据
        //区域指标排名
        optionsb: {},
        chartListB: [],
        //指标趋势变化
        options:{},
        chartListA: []
      }
    },
    mounted() {
      this.dataList = data.res;
      this.chartListB = this.dataList.regionalList.map(function(item){
        return +item
      });
      this.chartListA = this.dataList.indicatorTrends.map(function(item){
        return +item
      });
      this.optionsb = {
        chart: {
          type: 'bar',
          backgroundColor: '#062f43',
          style: {
            color: '#ffffff'
          }
        },
        title: {
          text: null
        },
        legend: {
          enabled: false
        },
        credits: {
          enabled: false
        },
        exporting: {
          enabled: false
        },
        xAxis: {
          categories: ['1.济南', '2.青岛', '3.济宁', '4.淄博', '5.莱芜'],
          title: {
            text: null
          },
          labels: {
            style: {
              color: '#fff',
              fontSize: ".266667rem"
            }
          }
        },
        yAxis: {
          visible: false
        },
        plotOptions: {
          bar: {
            dataLabels: {
              enabled: true,
              allowOverlap: true, // 允许数据标签重叠
              style: {
                textOutline: 'none', //文字描边
                color: "#fff"
              }
            }
          },
          series: {
            borderRadius: 2,
            borderColor: 'none'
          }
        },
        series: [{
          name: null,
          color: {
            linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
            stops: [
                [0, '#47d6f0'],
                [1, '#9381e1']
            ]
          },
          data: this.chartListB
        }]
      }
      this.options = {
        chart: { //基本信息
          type: 'column',
          backgroundColor: '#062f43',
          style: {
            color: '#ffffff'
          }
        },
        title: {
          text: null
        },
        legend: { //图例
          enabled: false
        },
        xAxis: { //X轴
          categories: [
            '1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月'
          ],
          lineColor: '#09435f', //轴线颜色
          tickPixelInterval: 50,
            //crosshair: true,
          labels: {
            style: {
              color: '#ffffff',
              fontSize: ".266667rem"
            }
          }
        },
        yAxis: {
          min: 0,
          max: 1000,
          gridLineColor: '#09435f', //网格线颜色
          tickAmount: 6,
          tickPixelInterval: 35,
          title: {
            text: null
          },
          labels: {
            style: {
              color: '#fff',
              fontSize: ".266667rem"
            }
          }
        },
        tooltip: {
          // head + 每个 point + footer 拼接成完整的 table
          headerFormat: '<span style="font-size:10px;color:#333;">{point.key}</span><table>',
          pointFormat: '<tr><td style="color:#333;padding:0">{series.name}: </td>' +
          '<td style="padding:0;color:#333;">{point.y:.1f}</td></tr>',
          footerFormat: '</table>',
          shared: true,
          useHTML: true
        },
        plotOptions: {
          column: {
            borderWidth: 0
          },
          series: {
            borderRadius: 5
          }
        },
        series: [{ //数据
          name: '',
          color: {
            linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
            stops: [
                [0, '#80e1be'],
                [1, '#48d6f0']
            ]
          },
          data: this.chartListA
        }],
        credits: { //版权信息
          enabled: false
        },
        exporting: { //导出按钮
          enabled: false
        }
      }

    },
    methods: {
      // getData() {
      //   let _this = this;
      //   this.axios.post("",{headers:{"content-Type":"application/json"}})
      //   .then(data => {
      //     if(data.code == "0000") {
      //       console.log(1)
      //     }else {
      //       console.log(2)
      //     }
      //   })
      //   .catch(error => {
      //     console.log(3)
      //   })
      // }
    },
    components: {
      Header,
      HighchartsContainer,
    }
  }
</script>

<style lang="less" scoped>
  .index-wrap {}
  .top {
    display: flex;
    align-items: center;
    padding: 0 .333333rem;
    margin-bottom: .333333rem;
    .h-2 {
      font-weight: bold;
      margin-right: .2rem;
      font-size: .4rem;
    }
  }
  .con {
    .mid-a {
      background: #062f43;
      padding: 0 .333333rem;
      .h-4 {
        border-bottom: 1px solid #13435b;
      }
      ul {
        li {
          display: flex;
          align-items: center;
          padding: .2rem .133333rem;
          border-bottom: 1px solid #13435b;
          p {
            font-size: .293333rem;
          }
          .t-name {
            color: #2dded4;
            width: 40%;
          }
          .t-num {
            width: 20%;
            font-family: digital;
            color: #f8e177;
            font-size: .346667rem;
          }
          .t-unit {
            width: 20%;
          }
          .t-note {
            width: 20%;
            text-align: right;
          }
        }
      }
    }
    .mid-b {
      margin: .2rem 0;
      display: flex;
      justify-content: space-between;
      div {
        background: #062f43;
      }
      .b-left {
        width: 40%;
        padding: .2rem .333333rem;
        .fbt {
          height: 80%;
          display: flex;
          align-items: center;
          img {
            width: 100%;
          }
        }
      }
      .b-right {
        width: 57%;
        padding: .2rem .333333rem;
      }
    }
    .bot {
      padding: 0 .333333rem .266667rem .333333rem;
      background: #062f43;
      .chart {

      }
    }
  }
</style>



